.widget-image {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    &:hover {
        .delete {
            display: flex;
        }
    }
    .delete {
        position: absolute;
        display: none;
        right: 5px;
        top: 5px;
        color: #fff;
        padding: .5rem;
        border-radius: 50%;
        cursor: pointer;
        &:hover {
            opacity: .8;
        }
    }
}